.slide-label {
  display: flex;
  font-size: 20px;
  font-weight: bold;
}
.slider {
  -webkit-appearance: none;
  width: 94%;
  height: 15px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  margin-right: 20px;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #4caf50;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #4caf50;
  cursor: pointer;
}
